@import "normalize.css";
@import "components/footer.css";
@import "components/header.css";
@import "components/stepper.css";
@import "components/price.css";
@import "components/registration.css";
@import "components/log-in.css";
@import "components/order.css";

@font-face{
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face{
  font-family: 'Roboto';
  src: url(../fonts/Roboto-Medium.ttf) format("truetype");
  font-weight: bold;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

html, body, main {
  height: 100%;
}

.article {
  transition: transform .3s;
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  flex: 1;
}

img {
  max-width: 100%;
  max-height: 100%;
}

a {
  color: #000000;
  text-decoration: none;
  transition: all 0.5s ease;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

p {
  margin: 0;
  font-size: 12px;
}

h1 {
  font-size: 60px;
  display: none;
}

h2 {
  font-size: 45px;
}

h3 {
  font-size: 18px;
}

h4 {
  font-size: 14px;
}

h5 {
  font-size: 10px;
}

h6 {
  font-size: 8px;
}

h1, h2, h3, h4, h5, h6 {
  margin-block-start: 0;
  margin-block-end: 0;
  padding: 0;
}

/* Базовые стили, конец */

.main {
  margin: 0 auto;
  max-width: 1920px;
}

@media (max-width: 1140px) {
  h2 {
      font-size: 38px;
  }
}

@media (max-width: 960px) {
  h2 {
      font-size: 30px;
  }

  h3 {
      font-size: 16px;
  }
}